import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ChartDataSets, ChartOptions } from 'chart.js';
import { Label, MultiDataSet } from 'ng2-charts';
import * as pbi from 'powerbi-client';
import { Observable } from 'rxjs/internal/Observable';
import { LoggedInUser } from 'src/app/models/loggedInUser';
import { AuthService } from 'src/app/services/auth.service';
import { HttpService } from 'src/app/services/http/http.service';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  chartData: ChartDataSets[] = [{ data: [65, 59, 80, 81, 56, 55, 40], label: 'Series A' } ];
  chartLabels: Label[] = ['January', 'February', 'March', 'April', 'May', 'June', 'July'];

  doughnutChartData: MultiDataSet = [[350, 450, 100],[50, 150, 120], [250, 130, 70]];

  public barChartOptions: ChartOptions = {
    responsive: true,
  };

  constructor(private router: Router,private httpService: HttpService, private authService: AuthService) { }

  userInfo: LoggedInUser;
  ngOnInit() {
    this.userInfo = this.authService.getLoggedInUserInfo()
    this.embedPowerBIReport();
  }

  goToManageUser() {
    this.router.navigate(['/dashboard/users']);
  }


  getReportEmbedToken(): Observable<any> {
    return this.httpService.get("GetEmbedInfo");

  }

  embedPowerBIReport() {
    this.getReportEmbedToken().subscribe((res) => {
      debugger
      let id = res.EmbedReport[0].ReportId;
      let embedUrl = res.EmbedReport[0].EmbedUrl;
      let token = res.EmbedToken.Token;
      const config = {
        type: 'report',
        tokenType: 1,
        embedUrl: embedUrl,
        accessToken: token,

      };

      let tokenExpiry = res.EmbedToken.Expiration;


       // Grab the reference to the div HTML element that will host the report.
    let reportContainer = <HTMLElement>document.getElementById('embedReport');

    // Embed the report and display it within the div container.
    let powerbi = new pbi.service.Service(pbi.factories.hpmFactory, pbi.factories.wpmpFactory, pbi.factories.routerFactory);
    let report = powerbi.embed(reportContainer, config);

    report.off("loaded");

    // Triggers when a report schema is successfully loaded
    report.on("loaded", function () {
        console.log("Report load successful");
    });

    // Clear any other rendered handler events
    report.off("rendered");

    // Triggers when a report is successfully embedded in UI
    report.on("rendered", function () {
        console.log("Report render successful");
    });

    // Clear any other error handler events
    report.off("error");

    // Handle embed errors
    report.on("error", function (event) {
        var errorMsg = event.detail;

        // Use errorMsg variable to log error in any destination of choice
        console.error(errorMsg);
        return;
    });
  })
  }

}
